@import './variables';

/*footer*/

footer.site-footer {
  background-color: var(--mainColor);
  color: white;

  .site-footer-content {
    @apply --pageWidth;
    padding-top: calc(var(--pad) * 2);
    padding-bottom: calc(var(--pad) * 2);
  }

  a {
    color: white;
  }

  h2 {
    display: none;
  }

  #menu-full-menu {
    @apply --inverseNavigation;
  }

  .site-info {
    background-color: var(--secondaryColor);
    color: white;
    text-align: center;
  }

  .site-info-content {
    @apply --pageWidth;
    @apply --paddingTopBottomFull;
    font-size: var(--smallerFontSize);

    span {
      white-space: nowrap;
    }
  }
}
