:root {

  /*typography*/
  /*--headlineFont: serif;*/
  --baseFontSize: 16px;
  --baseLineHeight: 1.6;
  --smallerFontSize: 0.85rem;
  --accessibilityFontSize: 0.85rem;
  --mainFont: {
    font-family: Lato, sans-serif;
    line-height: var(--baseLineHeight);
    font-weight: 300;
    font-stretch: normal;
    font-size: var(--baseFontSize);
  }
  --headlineFont: {
    font-family: 'Playfair Display', serif;
    line-height: var(--baseLineHeight);
    font-weight: bold;
    font-stretch: normal;
    font-size: var(--baseFontSize);
  }

  /*padding*/
  --pad: 1rem;

  /*colors*/
  --mainColor: rgb(21, 56, 112);
  --secondaryColor: rgb(158, 0, 18);
  --textColor: #333;
  --dimmedColor: #666;

  /*section specific*/
  --whyUsColor: rgb(114, 3, 129);
  --wayWeTeachColor: rgb(0, 134, 198);
  --dailyLifeColor: rgb(2, 183, 154);
  --ourResultsColor: rgb(171, 162, 1);

  /*measures*/
  --siteWidth: 980px; /* 980 */

  /*snippets*/
  --pageWidth: {
    width: 100%;
    max-width: var(--siteWidth);
    margin: 0 auto;
    padding: 0 var(--pad);
  }
  --paddingTopBottomFull: {
    padding-top: var(--pad);
    padding-bottom: var(--pad);
  }
  --paddingTopBottom: {
    padding-top: calc(var(--pad) / 2);
    padding-bottom: calc(var(--pad) / 2);
  }
}


@custom-media --small-screen only screen and (width <= 640px);
@custom-media --semilarge-screen only screen and (width <= 720px);
@custom-media --medium-screen only screen and (width > 641px) and (width <= 980px);
@custom-media --large-screen only screen and (width > 980px);
